.mainbar {
    width: 100%;
    height: 100vh;
    background: #eeeeee;
    display: flex;
    flex-direction: column;
    padding: 20px 40px;

    .header {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .header__h2 {
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        
        color: #338FE3;
        
    }

    .header__h2_dark {
        color: #FBC374;
    }

    .mode {
        display: flex;
        flex-direction: row;
        align-items: center;
        .mode__p {
            font-weight: 400;
            font-size: 24px;
            line-height: 29px;
            opacity: .8;
            margin-right: 15px;
        }
    }

    .highlights {
        width: 100%;
        display: flex;
        flex-direction: column;

        .highlights__h2 {
            font-weight: 700;
            font-size: 32px;
            line-height: 39px;
        }

        .hl__p {
            font-weight: 400;
            font-size: 24px;
            line-height: 29px;
            text-align: right;
        }

        .hl__wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

        }
    }

    .blocks {
        display: grid;
        grid-template-columns: 430px 430px;
        justify-content: space-between;
        grid-template-rows: 160px 160px;
        grid-column-gap: 70px;
        grid-row-gap: 50px;
        width: 100%;
        margin: 30px 0 0;

        .block {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            background: #FFFFFF;
            border-radius: 10px;
            padding: 25px 30px;
        }

        .block_dark {
            background: #656565;
        }

        .block__label {

            font-weight: 400;
            font-size: 24px;
            line-height: 29px;
        }

        .block__wrapper {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            align-items: center;
        }

        .block__wrapper2 {
            display: flex;
            align-items: flex-end;
        }

        .block__p {
            font-weight: 700;
            font-size: 50px;
            line-height: 61px;

            margin-right: 8px;
        }

        .block__un {
            font-weight: 400;

            font-size: 28px;
            line-height: 61px;
        }

        .block__icon {
            width: 100px;
            height: 100px;
            margin: 0;
            padding: 0;
        }

    }

    .week {
        width: 100%; 

        margin: 30px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .week__day {
        width: 150px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px;
        border-radius: 10px;
        background-color: white;
    }

    .week__day-p {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        margin-bottom: 15px;
    }

    .week__day-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .temp1 {
        width: 40px;
        margin: 0 2px 0 0;
    }

    .week__day-img {
        width: 80px;
        height: 80px;
        margin: 0;
    }
    .week__day_dark {
        background: #656565;
    }

    
}

.mainbar_dark {
    background: #363636;
}

